<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" clearable v-model="queryInfo.query"  @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList" ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table stripe border :data="goodsList" style="width: 100%">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label="" ></el-table-column>
        <el-table-column prop="goods_price" label="" width="95"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="70"></el-table-column>
        <el-table-column prop="add_time" label="" width="140">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <!-- eslint-disable-next-line -->
          <template slot-scope="scope">
            <!-- {{scope.row.mg_state}}  scopeprops -->
            <!--  -->
            <el-button  @click="showEditDialog(scope.row.id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
            <!--  -->
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeGoodsByid(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
        queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10
      },
      goodsList: [],
      total: 0
    }
  },
  methods: {
      async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      // this.$message.success('!')
      this.goodsList = res.data.goods
      this.total = res.data.total
    },
    showEditDialog() {
    },
    async removeGoodsByid(id) {
      console.log('xxxx', id)
      // 
      const res = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err => err)
        //   confirm
        //   cancle
        console.log(res)
        if (res !== 'confirm') {
          return this.$message.info('')
        }
        const { data } = await this.$http.delete('goods/' + id)
        if (data.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 
        this.getGoodsList()
    },
    handleSizeChange(newSize) {
       this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    goAddpage() {
      this.$router.push('/goods/add')
    }
  },
  created() {
      this.getGoodsList()
  }
}
</script>

<style scoped>
</style>
